<template>
  <div class="tabs">
    <div
      class="tabs_wrap"
      :style="{ 'justify-content': arrange ? 'space-between' : 'flex-start' }"
    >
      <div
        v-for="tab in tabs"
        :key="tab.name"
        :class="['tabs_wrap_tab', currentTab == tab.name ? 'active' : '']"
      >
        <div><font-awesome-icon icon="info-circle" v-if="tab.info" /></div>
        <div
          @click="
            () => {
              currentTab = tab.name
              $emit('change', tab.name)
            }
          "
        >
          {{ tab.name }}
        </div>
        <div>
          <font-awesome-icon icon="caret-down" v-if="tab.replacement" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tabs',
  props: {
    tabs: Array,
    arrange: Boolean,
  },
  data: () => ({
    currentTab: '',
  }),
  mounted() {
    this.currentTab = this.tabs[0].name
  },
}
</script>

<style lang="scss" scoped>
@import '../../../scss/smart-grid';
@import '../../../scss/variables';

.tabs {
  border-bottom: 1px solid $cyan-alpha;
  height: 30px;
  padding: 0 20px;
  color: white;
  margin: 10px auto;

  &_wrap {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &_tab {
      height: 100%;
      color: $cyan;
      transition: all 0.2s;
      user-select: none;
      display: flex;
      border-bottom: 4px transparent solid;

      div {
        color: $cyan;
        cursor: pointer;
        min-width: 30px;
        height: 100%;

        * {
          color: $cyan;
        }
      }

      div:hover {
        * {
          color: white;
        }
      }

      div:hover {
        color: white;
      }

      &.active div {
        color: white;
      }

      &.active {
        border-bottom: 4px $light-blue solid;
      }
    }
  }
}
</style>
