<template>
  <div class="page">
    <Background />
    <div :class="['container', viewLoaded ? 'loaded' : '']">
      <rates-card />
      <place-order-card />
      <live-orders-card />
      <orders-card />
      <VueTradingView
        :options="{
          width: '100%',
          height: 610,
          symbol: 'XBT',
          interval: '1',
          timezone: 'Etc/UTC',
          theme: 'Dark',
          style: '2',
          locale: 'en',
          toolbar_bg: '#f1f3f6',
          enable_publishing: false,
          withdateranges: true,
          studies: ['MAVolumeWeighted@tv-basicstudies'],
        }"
      />
    </div>
  </div>
</template>

<script>
import Background from '@/components/views/wallet/Background.vue'
import { mapGetters } from 'vuex'
import VueTradingView from 'vue-trading-view'

import RatesCard from '../components/views/trading/cards/RatesCard'
import PlaceOrderCard from '../components/views/trading/cards/PlaceOrderCard'
import OrdersCard from '../components/views/trading/cards/OrdersCard'
import LiveOrdersCard from '../components/views/trading/cards/LiveOrdersCard'

import axios from 'axios'

export default {
  name: 'Trading',
  components: {
    Background,
    VueTradingView,
    RatesCard,
    PlaceOrderCard,
    OrdersCard,
    LiveOrdersCard,
  },
  provide() {
    return {
      profile: this.profile,
      rates: this.rates,
    }
  },
  data: () => ({
    viewLoaded: false,
    currency: 'BTC',
    currencies: ['BTC', 'ETH', 'LTC'],
    rates: {},
  }),
  watch: {},
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
    }),
  },
  methods: {
    checkViewNode() {
      var view = this.$el.querySelector('#vue-trading-view')

      if (view && view.childElementCount) {
        this.viewLoaded = true
        this.$store.dispatch('preloader/startAfterLoading')
        this.$root.$emit('change-currency', 'BTC')
      } else {
        setTimeout(this.checkViewNode, 2000)
      }
    },
    getCoinRate(NET) {
      return new Promise(resolve => {
        const pending = []

        pending.push(
          axios.get(
            `https://min-api.cryptocompare.com/data/generateAvg?fsym=${NET}&tsym=USD&e=Kraken`,
          ),
        )

        pending.push(
          axios.get(
            `https://min-api.cryptocompare.com/data/price?fsym=${NET}&tsyms=USD`,
          ),
        )

        Promise.all(pending).then(([{ data: history }, { data: current }]) => {
          resolve({
            NET,
            price: current.USD,
            change: history.DISPLAY?.CHANGEPCT24HOUR,
            raw: history.RAW,
            display: history.DISPLAY,
          })
        })
      })
    },
    async updateCurrencies() {
      if (this.$route.name != 'Trading') return

      var pending = this.currencies.map(c => this.getCoinRate(c))

      for await (let p of pending) {
        p.price = p.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
        this.rates[p.NET] = p
      }

      document.title = `${this.rates[this.currency].price} (${
        this.currency
      }/USD) | Onyxian - Trading`

      setTimeout(this.updateCurrencies, 20000)
    },
    changeCurrency(net) {
      this.currency = net
      document.title = `${this.rates[this.currency].price} (${
        this.currency
      }/USD) | Onyxian - Trading`
    },
  },
  mounted() {
    this.checkViewNode()
    this.updateCurrencies()

    this.$root.$on('change-currency', this.changeCurrency)
  },
  destroyed() {
    this.$root.$off('change-currency', this.changeCurrency)
  },
}
</script>

